.header-navbar .navbar-brand.c9-custom-logo > .c9-home-logo {
  height: 92px; }

.header-navbar .navbar-small .navbar-brand.c9-custom-logo > .c9-home-logo {
  height: 55px; }

.header-navbar .justify-content-center {
  -webkit-box-pack: end flex-end !important;
  -ms-flex-pack: end flex-end !important;
  justify-content: flex-end !important; }

.header-navbar .navbar .nav .nav-item,
.header-navbar .navbar .nav .nav-item .dropdown-item,
.header-navbar .navbar .nav .nav-item .nav-link,
.header-navbar .navbar .nav .search {
  line-height: 1; }

.header-navbar .navbar .nav .search .nav-search a {
  font-size: 14px; }

.header-navbar .nav .nav-item .dropdown-menu {
  background-color: #ca292c;
  border-radius: 0px;
  margin-top: 1em;
  border: 0px;
  opacity: 0;
  left: auto;
  right: 0px;
  -webkit-transition: opacity 3s;
  transition: opacity 3s;
  -webkit-transition-delay: 1s;
  transition-delay: 1s; }

.header-navbar .nav .nav-item .dropdown-menu.show {
  opacity: 1;
  -webkit-transition: opacity 3s;
  transition: opacity 3s; }

.header-navbar .nav .nav-item .dropdown-menu .dropdown-item:hover {
  background-color: #c1081f;
  color: #ffffff; }

.header-navbar .navbar .nav .nav-item .nav-link {
  border: 1px solid transparent; }

/****************************************************************************************/
/* nav link hover*/
/****************************************************************************************/
.header-navbar .navbar .nav .nav-item .dropdown-item:hover,
.header-navbar .navbar .nav .nav-item .nav-link:hover {
  color: #d2d2d2; }

.header-navbar .navbar .nav .nav-item .nav-link {
  -webkit-transition: border-color 1s ease-out, color 1s ease-out;
  transition: border-color 1s ease-out, color 1s ease-out; }

.header-navbar .navbar .nav .nav-item {
  margin: 0px 5px; }

.header-navbar .navbar .nav .nav-item .nav-link:hover,
.header-navbar .navbar .nav .nav-item.active .nav-link {
  color: #55f0ff; }

.header-navbar .navbar .nav .nav-item > .nav-link:focus,
.header-navbar .navbar .nav .nav-item > .nav-link:active {
  color: #55f0ff; }

.header-navbar .navbar.navbar-small .nav .nav-item.active > .nav-link {
  background-color: #55f0ff;
  color: #ffffff; }

/****************************************************************************************/
/* mobile navigation background*/
/****************************************************************************************/
@media only screen and (max-width: 667px) {
  .header-navbar .navbar {
    background-color: #ca292c; } }

/****************************************************************************************/
/* transparent page header navigation on desktop */
/****************************************************************************************/
@media only screen and (min-width: 768px) {
